import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Container, FormGroup, FormInput, Row, Col } from "shards-react";
import PageTitle from "../../components/common/PageTitle";
import { getAppointmentsEndpoint } from "../../apiConstants/apiConstants";
import { getAppointments } from "../../actions/patient/patient";
import PageSpinner from "../../components/common/PageSpinner";

export default function Appointments() {
  const user = JSON.parse(localStorage.getItem("loggedInUser"));
  const dispatch = useDispatch();
  const getAppointmentsState = useSelector((state) => state.getAppointments);
  const [appointmentsState, setAppointmentsState] = useState();
  const [appointments, setAppointments] = useState();

  useEffect(() => {
    let endpoint = getAppointmentsEndpoint + user.companyCode;
    dispatch(getAppointments(endpoint));
  }, []);

  useEffect(() => {
    console.log("Appointments", getAppointmentsState.data);
    getAppointmentsState.data &&
      setAppointmentsState(
        getAppointmentsState.data.filter((item) => item.Patient.id === user.id)
      );
  }, [getAppointmentsState]);

  useEffect(() => {
    appointmentsState && setAppointments(appointmentsState);
  }, [appointmentsState]);

  const handleSearch = ({ target }) => {
    if (target.value === "") {
      setAppointments(appointmentsState);
    } else {
      setAppointments(
        appointmentsState.filter((item) =>
          item.Appointment.appointmentNotes.includes(target.value)
        )
      );
    }
  };

  if (!appointments) {
    return <PageSpinner />;
  }

  return (
    <Container>
      <Row noGutters className="page-header py-4">
        <PageTitle
          sm="4"
          title="Appointments"
          subtitle="Dashboard"
          className="text-sm-left"
        />
      </Row>
      <Col>
        <div>
          <FormGroup className="row d-flex align-items-center">
            <label className="mb-0 mr-3">Search Keyword:</label>
            <FormInput onChange={handleSearch} style={{width: 200}}/>
          </FormGroup>
        </div>

        <Row>
          {
            appointments.length && appointments.map((item) => (
              <Col className="col-lg-4">
                
              </Col>
            ))
          }
        </Row>
      </Col>
    </Container>
  );
}
